
* {
    font-family: "IBM Plex Sans", sans-serif;
}

.map {
    border-top: 1px solid rgb(233, 233, 233);
}

.items {
    margin-top: 55px;
    min-height: 100vh;
}

.diplom{
    background-color: rgb(255, 255, 255);
    
    margin-bottom: 50px;
}

.diplom button {
    padding: 8px 20px;
    font-size: 18px;
    font-weight: 500;
    background-color: rgb(0,98,255);
    color: white;
    cursor: pointer;
    transition: .2s ease-in-out;
    text-decoration: none;
    border: none;
    border-radius: 30px;
}

.diplom button:hover {
    background-color: rgba(0, 98, 255, 0.753);
    transform: translateY(-2px);
}

.diplominfo {
    text-align: center;
    justify-content: space-around;
    padding: 20px;
}

.diplominfo h1 {
    color: black;
}

.diplominfo p {
    color: black;
    font-weight: 500;
    padding-bottom: 10px;
}

.dipimg {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.897);
    width: 100%;
    height: 100%;
    text-align: center;
    z-index: 100000;
    top: 0;
    left: 0;
    padding-top: 70px;
    visibility: hidden;
}

.dipimg__active {
    visibility: visible;
}

.dipimg__active span {
    position: absolute;
    margin: 20px;
    color: white;
    cursor: pointer;
    top: 0;
    right: 0;
}


.otkr {
   text-align: center;
   padding-top: 120px;
   padding-bottom: 100px;
   background-color: rgb(246,248,252);
   border-bottom: 1px solid rgb(233, 233, 233);
}

.otkr p {
    margin: 40px;
    font-size: 18px;
    font-weight: 600;
    color: rgb(49, 49, 49);
}

.otkr h1 {
    color: rgb(0,98,255);
    font-size:60px;
    margin: 0 20px;
}

.otkr a {
    background-color: rgb(0,98,255);
    color: white;
    font-size: 18px;
    text-decoration: none;
    border-radius: 50px;
    padding: 10px 15px;
    font-weight: 500;
    transition: .2s ease-in-out;
}

.otkr a:hover {
    background-color: rgba(0, 98, 255, 0.753);
    transform: translateY(-2px);
}

.partners2 {
    display: none;
    margin: 40px 0;
}



.clpbtns {
    display: flex;
    align-items: center;
    display: flex;
    margin: 0 auto;
    width: 265px; 
}

.collegeinfo {
    max-width: 1500px;
    margin: 0 auto;
    text-align: center;
    align-items: center;
    display: flex;
}

.collegetext {
    margin-left: 20px;
    margin-right: 20px;
}

.collegeimg {
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
    transition: .3s ease-in-out;
    
}

.collegeimg img {
    width: calc(50% - 10px);
}
.collegeinfo:hover > .collegeimg{

    animation: shake 0.5s;
    animation-iteration-count: infinite;
    
}

@keyframes shake {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(5deg); }
    100% { transform: rotate(0deg); }
  }

.collegeinfo h1 {
    font-size: 35px;
}

.collegeinfo p {
    font-size: 18px;
    font-weight: 500;
    color: rgb(49, 49, 49);
}

.chatbot {
    margin-top: 50px;
    width: 350px;
    display: none;
    border: 1px solid rgb(233, 233, 233);
    border-radius: 11px;
    z-index: 0;
    margin-left: 5px;
    margin-right: 5px;
}

.bottitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 15px;
}


.chatbot__active {
    display: inline-block;
}

.chatbot button{
    cursor: pointer;
}

.chatbot a {
    color: black;
    font-size: 15px;
}

.questimg__inactive {
    display: none;
}

.closeb {
    font-size: 22px;
    cursor: pointer;
    background-color: rgb(235, 235, 235);
    color: rgb(175, 174, 174);
    padding: 7px;
    border-radius: 20px;
    transition: .3s ease-in-out;
}


.chatbot div {
    margin-top: 0;
}

.partners {
    margin: 40px auto;
    text-align: center;
}

.partnersimg {
    max-width: calc(13% - 10px);
    margin: 0 40px;
}


@keyframes scroll {
    from {
        transform: translateX(0);
    }
    to {
        transform: translateX(calc(-1000px));
    }
}

@keyframes updown {
    0% {
        bottom: 20px;
    }

    50% {
        bottom: 0px;
    }

    100% {
        bottom: 20px;
    }
}




@media (max-width: 1600px) {


    .dipimg img{
        width: 60%;
    }


}

@media (max-width: 1050px) {

    .partners {
            display: none;
    }

    .partners2 {
        display: block;
    }


    .collegetext h1 {
        font-size: 30px;
        margin-top: 40px;
    }

    .collegetext p {
        font-size: 16px;
    }

    .collegeinfo {
        display: flex;
        flex-direction: column-reverse;
    }

    .collegeimg img {
        width: calc(40% - 10px);
    }



    .otkr h1 {
        font-size: 45px;
    }


    .dipimg img{
        width: 90%;
    }

    .otkr {
        padding-top: 80px;
    }

    


}


@media (max-width: 375px) {


    .otkr h1{

        font-size: 40px;
        margin-top: 50px;
        
    }

}